import React, { Component } from 'react';
import Modal from '../Modal/Modal';

import CaseIndustrial1 from '../../img/cases/industrial/1.png';
import CaseIndustrial2 from '../../img/cases/industrial/2.png';
import CaseIndustrial3 from '../../img/cases/industrial/3.png';
import CaseIndustrial4 from '../../img/cases/industrial/4.png';
import CaseIndustrial5 from '../../img/cases/industrial/5.jpg';
import CaseIndustrial6 from '../../img/cases/industrial/6.png';

import CaseResidencial1 from "../../img/cases/residencial/1.jpg";
import CaseResidencial2 from "../../img/cases/residencial/2.jpg";

import './Cases.scss';

class Cases extends Component {
  constructor(props) {
    super(props);
    
    this.state = {
      cases: [
        {
          title: 'Industrial',
          text: (
            <div>
              <h3>Contato</h3>
              <p>A Quops Engenharia iniciou sua relao com a Tabacaria Guanabara (Londrina/PR) dada necessidade do cliente em melhorar o seu processo produtivo e ampliar sua produo. Almejava-se viabilizao de nova sede para sua Indstria, com um novo layout da produo e setor administrativo. Foi realizado oramento para elaborao de layout da indstria e projeto arquitetnico compatibilizado com os projetos complementares.</p>
              <h3>Diferencial</h3>
              <p>Com uso do software Revit, foi fornecida maquete 3D do empreendimento e oramento com lista de materiais e servios necessrios para execuo do projeto. A indstria passara a ter maior poder de negociao com os futuros fornecedores, uma vez que o escopo da obra fora previamente definido. A diferena entre oramentos realizados por outras empresas diferiam cerca de 2 milhes de reais (30% do valor total do projeto).</p>
              <h3>Processo</h3>
              <p>Todas as maquinas existentes no barraco anterior foram modeladas em 3D, utilizando os programas CREO 3.0 e SolidWorks. Paralelamente, as necessidades dos funcionrios foram estudadas atravs de entrevistas, visando otimizao de seus deslocamentos, comunicao e ergonomia. Todo o equipamento existente foi aproveitado, com adio de esteiras e automao de alguns processos.</p>
              <p>O projeto arquitetnico seguiu o layout industrial definido, objetivando obra limpa, sustentvel, rpida e barata. Foi adotado sistema de reaproveitamento de guas pluviais, com sua reutilizao nos banheiros, vestirios e limpeza do piso industrial.</p>
            </div>
          ),
          imgs: [CaseIndustrial1, CaseIndustrial2, CaseIndustrial3, CaseIndustrial4, CaseIndustrial5, CaseIndustrial6]
        },
        {
          title: 'Residencial',
          text: (
            <div>
              <p>Com o sonho de construir sua casa prpria em condomnio fechado, a cliente Adriana Pelegrini, de Presidente Prudente/SP, encontrava dificuldade diante dos valores abusivos cobrados por construtores da cidade, chegando a R$ 3.000,00/m.</p>
              <p>Ao contatar a Quops Engenharia, foi realizado estudo de necessidades e limitao financeira da cliente para viabilizao do empreendimento. Com auxlio do software Revit, desenvolveu-se projeto em 3D de sobrado com 230 m que visava otimizao dos espaos, simplicidade e esttica. Foram utilizados mtodos construtivos alternativos, com fechamento em bloco de concreto, pisos em concreto polido e cimento queimado, revestimento interno em gesso e externo em textura e lajes protendidas pr-moldadas.</p>
              <p>Todas as solues foram aprovadas pela cliente Adriana, que se disse satisfeita com a reduo de custos em aproximadamente 50%, conseguindo realizar o sonho da casa prpria com preo justo e atendendo suas necessidades de moradia.</p>
            </div>
          ),
          imgs: [CaseResidencial1, CaseResidencial2]
        }
      ]
    };
  }
  
  openModal(typeModal) {
    this.modal.showModal(this.state.cases[typeModal]);
  }

  render() {
    return (
      <div id='cases'>
        <div>
          <h2>Cases</h2>
          <div className='pagetitle-separator'>
            <div className='pagetitle-separator-border'>
              <div className='pagetitle-separator-box'></div>
            </div>
          </div>
          <ul className='cases-list'>
            <li className='cases-item' style={{backgroundImage: 'url(' + CaseIndustrial1 + ')'}}>
              <div className='cases-text'>
                <h3>Industrial</h3>
                <a title='Clique aqui para mais informaes' onClick={() => this.openModal(0)}>
                  Leia mais
                </a>
              </div>
              <div className='cases-item-background' style={{backgroundImage: 'url(' + CaseIndustrial1 + ')'}}></div>
            </li>
            <li className='cases-item'>
              <div className='cases-text'>
                <h3>Residencial</h3>
                <a title='Clique aqui para mais informaes' onClick={() => this.openModal(1)}>
                  Leia mais
                </a>
              </div>
              <div className='cases-item-background' style={{backgroundImage: 'url(' + CaseResidencial1 + ')'}}></div>
            </li>
          </ul>
        </div>
        {
          <Modal onRef={(modal) => (this.modal = modal)}/>
        }
      </div>
    );
  }
}

export default Cases;


